import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http'

export interface StateData {
  

  full_county_name: string;
  ratio: string;
  number_missing: string;
  county_population: string;

}

export interface CountyData{
  city : string;
  city_counts : string;

}



@Injectable({
  providedIn: 'root'
})
export class StatesService {

  constructor(
    public http: HttpClient
  ) { }

  //gets the state break down
  getStateFrequencies(state: string)  {
    return this.http.get<StateData[]>('/api/state/' + state + "/")
  }


  //get the state breakdown but a post 
  getCountyFreq(state: string, start_date : string, end_date: string){
    var httpOptions = {headers: new HttpHeaders({'Content-Type':  'application/json'})};
    var body = { "state" : state, "start": start_date, "end" : end_date };

    return this.http.post<StateData[]>("/api/state/" + state, body, httpOptions)
  }

  //gets the frequencies of every city within a given county
  getCityFrequencies(state : string, county : string, start_date : string, end_date : string){
    //get the counts of different cities
    var httpOptions = {headers: new HttpHeaders({'Content-Type':  'application/json'})};
    var body = { "state" : state, "county" : county,  "start": start_date, "end" : end_date };

    return this.http.post<any[]>("/api/county/" + county , body, httpOptions)
  }


}
