import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';

export interface People {
  first_name: string;
  middle_name: string;
  last_name: string;
  current_age_to: string;
  current_age_from: string;
  computed_missing_age_min: string;
  computed_missing_age_max: string;
  city: string;
  full_county_name: string;
  full_state_state: string;
  

}


export interface DemographicsData {
  //need a way to get race->gender->age
}



@Injectable({
  providedIn: 'root'
})
export class PeopleService {
   
  constructor(
    public http: HttpClient
  ) { }
  getPeople() {
    return this.http.get<People[]>('/api/people');
    }
  getFrequencyRates() {
    return this.http.get<any[]>('/api/frequency/data');
      }

 
  getFrequencyRatesDates(start_date : string, end_date : string){
    const httpOptions = {headers: new HttpHeaders({'Content-Type':  'application/json'})};
    const body = { "start": start_date, "end" : end_date };

    return this.http.post<any[]>("/api/frequency/data ", body, httpOptions)   
   
  }

  getGenderBreakdown(){
    return this.http.get<any[]>('/api/demographics/gender ');
  }

  getGenderAgeBreakdown(){
    return this.http.get<any[]>('/api/demographics/gender/age');
  }

  getEthnicityBreakdown(){
    return this.http.get<any[]>('/api/demographics/ethnicity/');
  }

  getFrequencyCounts() {
    return this.http.get<any[]>('/api/frequency/raw');
        }

  getDemographicsData(state: string,  start_date: string, end_date: string){
    //get the demographics of the current chosen state/county/city
    const httpOptions = {headers: new HttpHeaders({'Content-Type':  'application/json'})};
    const body = { "state" : state, "start": start_date, "end" : end_date };

    return this.http.post<any[]>("/api/county-demographics ", body, httpOptions)
  }

}
