import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class StatisticServiceService {

  constructor(  public http: HttpClient) { }

  getAverageAge(state : string, start_date : string, end_date : string){
    //get the counts of different cities
    var httpOptions = {headers: new HttpHeaders({'Content-Type':  'application/json'})};
    var body = { "state" : state, "start": start_date, "end" : end_date };

    return this.http.post<any[]>("/api/stats/avg-age" , body, httpOptions)
  }



  //get the top counties by raw missing persons counts by given year
  getTopFrequencies(start_date :string, end_date : string){
    var httpOptions = {headers: new HttpHeaders({'Content-Type':  'application/json'})};
    var body = {"start": start_date, "end" : end_date };

    return this.http.post<any[]>("/api/yearly_count" , body, httpOptions)
  }


  //are there any demographics that are overly represented in the namus database
  getInsights(start_date :string, end_date : string){
    var httpOptions = {headers: new HttpHeaders({'Content-Type':  'application/json'})};
    var body = {"start": start_date, "end" : end_date };

    return this.http.post<any[]>("/api/stats/top" , body, httpOptions)
  }
  


}
