import { Component, OnInit } from '@angular/core';
import { PeopleService } from '../people.service';
import { FormGroup, FormControl } from '@angular/forms';
import { StatisticServiceService } from '../statistic-service.service';

declare const Highcharts: any;




@Component({
  selector: 'app-insights',
  templateUrl: './insights.component.html',
  styleUrls: ['./insights.component.css']
})
export class InsightsComponent implements OnInit {


  
    public range = new FormGroup({
        start_form: new FormControl('2015-01-01'),
        end_form: new FormControl('2015-12-31'),
      });
    
    
    public date_range = {
        start_date : '2015-01-01',
        end_date : '2015-12-31'
      }
    
    currentYear : string;


    

  constructor(private statService: StatisticServiceService, private peopleService: PeopleService) { 

    this.peopleService.getFrequencyCounts().subscribe(data=>{
        const county = []
        const counts = []
        data.forEach(entry => {
            county.push(entry.full_county_name + "," + entry.state_name);
            counts.push(entry.frequentCount)
          });
  

        this.renderFrequencyChart("Namus Entries by County", "freq", "Number Missing", county, counts)
  
    });


    this.peopleService.getGenderAgeBreakdown().subscribe(

       
        data=>{
            const fcounts = [];
            const mcounts = [];

          
              
            fcounts.push(data[0].fchildren, data[0].fteen, data[0].fyoung_adult, data[0].fadult, data[0].folder_adult, data[0].fsenior);
            mcounts.push(data[0].mchildren, data[0].mteen, data[0].myoung_adult, data[0].madult, data[0].molder_adult, data[0].msenior);
            this.renderAgeStackBar(fcounts, mcounts);
        }
            );

    

    this.statService.getTopFrequencies(this.range.get('start_form').value, this.range.get('end_form').value).subscribe(data=>{
        //gets the counties with the top counts
        const county = [];
        const count = [];

        data.forEach(entry=>{
            county.push(entry.full_county_name + "," + entry.state_name);
            count.push(entry.frequentCount)

        });
        this.renderFrequencyChart("Number of Missing Persons by County", "raw", "Number Missing", county, count)

        
    })

    this.peopleService.getGenderBreakdown().subscribe(data=>{

        const num_women = data[0].female;
        const num_men = data[0].male;

        
        var fval = {
            name: 'Female',
            y : data[0].female

        }

        var mval = {
            name: 'Male',
            y : data[0].male
        }

        var d= []
        d.push(fval)
        d.push(mval)

        this.renderGenderPieChart(d)
    });

    

    this.peopleService.getFrequencyRatesDates(this.range.get('start_form').value, this.range.get('end_form').value).subscribe(
      data=> {
      

        const people = [];
        const counts = [];

        data.forEach(person => {
          people.push(person.county + "," + person.state_name);
          counts.push(person.frequentCount)
        });



        this.renderFrequencyChart("Ratio of Missing People By County Population", "frequency_rates", "Ratio", people, counts)

       
      
        
      }
    );
   
  }



  ngAfterViewInit(){

  }

  ngOnInit(): void {


   

   


    
  }



  onSubmit(){

    this.statService.getTopFrequencies(this.range.get('start_form').value, this.range.get('end_form').value).subscribe(data=>{
        //gets the counties with the top counts
        const county = [];
        const count = [];

        data.forEach(entry=>{
            county.push(entry.full_county_name + "," + entry.state_name);
            count.push(entry.frequentCount)

        });
        this.renderFrequencyChart("Number of Missing Persons by County", "raw", "Number Missing", county, count)

        
    })
    

    this.peopleService.getFrequencyRatesDates(this.range.get('start_form').value, this.range.get('end_form').value).subscribe(
        data=> {
        
  
          const people = [];
          const counts = [];
  
          data.forEach(person => {
            people.push(person.county + "," + person.state_name);
            counts.push(person.frequentCount)
          });
  
  
  
          this.renderFrequencyChart("Ratio of Missing People By County Population", "frequency_rates", "Ratio", people, counts)
  
         
        
          
        }
      );




  }
  renderChart(people: string[], counts: number[]) {
    Highcharts.chart('frequency_rates', { chart: { type: 'column'},
    title: { text: 'No. of Missing by County'},
    xAxis: {categories: people, title: {text: 'County'}, labels : {step : 1, rotation: -45, skew3d: true}},
    yAxis: {min: 0,title: {text: 'No. Missing'},
    labels: {overflow: 'justify'}},
    plotOptions: {bar: {dataLabels: { enabled: true}}}, 
    legend: {enabled: false},
    credits: {enabled: false},
    series: [{ data: counts }]});
  }

  
  //data breakdown - demographics: age, gender, ethnicity


  renderFrequencyChart(titleName: string, chartName : string, yaxis: string, counties : string[], counts : string[]){
    Highcharts.chart(chartName, {
      chart: {
          renderTo:chartName,
          type: 'column'
      },
      title: {
          text: titleName
      },
      subtitle: {
          text: 'Source: https://public.opendatasoft.com'
      },
      xAxis: {
          categories: counties,
          crosshair: true
      },
      yAxis: {
          class: 'highcharts-colors-2',
          min: 0,
          title: {
              text: yaxis
          }
      },
      tooltip: {
          headerFormat: '<span style="font-size:10px">{point.key}</span><table>',
          pointFormat: '<tr><td style="color:{series.color};padding:0">{series.name}: </td>' +
              '<td style="padding:0"><b>{point.y:.1f}</b></td></tr>',
          footerFormat: '</table>',
          shared: true,
          useHTML: true
      },
      plotOptions: {
          column: {
              pointPadding: 0.2,
              borderWidth: 0
          }
      },
      series: [{
          name: 'Namus Entries per County',
          data: counts
  
      }]
  });

  }




  renderGenderPieChart(data : string[]){
        // Build the chart

        console.log(data)
    Highcharts.chart('gender_piechart', {
      chart: {
          //renderTo: "gender",
          plotBackgroundColor: null,
          plotBorderWidth: null,
          plotShadow: false,
          type: 'pie'
      },
      title: {
          text: 'Gender Breakdown'
      },
      tooltip: {
          pointFormat: '{series.name}: <b>{point.percentage:.1f}%</b>'
      },
      accessibility: {
          point: {
              valueSuffix: '%'
          }
      },
      plotOptions: {
          pie: {
              allowPointSelect: true,
              cursor: 'pointer',
              dataLabels: {
                  enabled: false
              },
              showInLegend: true
          }
      },
      series: [{ 
        name: 'Gender',
        colorByPoint: true,
        data: data
      }]
      
    });
  }



  renderAgeStackBar(fcounts : string[], mcounts : string[]){
    Highcharts.chart('age_gender', {
      chart: {
          type: 'bar'
      },
      title: {
          text: 'Age Breakdown'
      },
      xAxis: {
          categories:  ['0-10', '11-17', '18-26', '27-44', '45-65', '66+']
      },
      yAxis: {
          min: 0,
          title: {
              text: ''
          }
      },
      legend: {
          reversed: true
      },
      plotOptions: {
          series: {
              stacking: 'normal'
          }
      },
      series: [{
          name: 'Female',
          data: fcounts
      }, {
          name: 'Male',
          data: mcounts
      }]
  });
  }
















}

