  getStackChartData(fethnicity: string[] , methnicity : string[], fcounts : string[], mcounts: string[]){


    // console.log(mcounts)
    // console.log(ethnicity)
     
 
 
     var series = []
 
     var len = fethnicity.length
    
     
     for (var i = 0; i < len; i++){
       var mdict = {
         name: methnicity[i],
         data: mcounts[i],
         stack: "male"
 
       }
       var fdict = {
         name: fethnicity[i],
         data: fcounts[i],
         stack: "female"
   
       }
       
       series.push(mdict);
       series.push(fdict);
     }
 
 
 
 
 
 
 
     
     return series
 
     //this.renderStackChart(series);
   }
 
 
  renderStack(stackdata: string[]){
 
    Highcharts.chart('demo', {

      chart: {
          type: 'column'
      },
  
      title: {
          text: `Total Missing Persons, grouped by gender : ${this.stateName}, ${this.start_date} to ${this.end_date}`
      },
  
      xAxis: {
          title: {
            text: "Age Ranges of Missing People"
          },
          categories: ['0-10', '11-17', '18-26', '27-44', '45-65', '66+']
      },
  
      yAxis: {
          allowDecimals: false,
          min: 0,
          title: {
              text: 'Number Of People Missing'
          }
      },
  
      tooltip: {
          formatter: function () {
              return '<b>' + this.x + '</b><br/>' +
                  this.series.name + ': ' + this.y + '<br/>' +
                  'Total: ' + this.point.stackTotal;
          }
      },
  
      plotOptions: {
          column: {
              stacking: 'normal'
          }
      },
  
      series: stackdata
  });
 
  }



